<template>
  <div class="people" >
    <div class="title">
      <h2>{{peoples.title}}</h2>
    </div>
    <crumbs></crumbs>
    <div class="introduce">
      <h3>详情信息</h3>
      <p>CONTACT US</p>
      <div class="things">
        <div v-for="(an,index) in peoples.children" :key="an.text1">
          <h4>{{an.text1}}</h4>
          <p>{{an.text11}}</p>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import { peoples } from "~/data";
import crumbs from "~/components/crumbs.vue";

export default {
  data() {
    return {
      peoples: peoples
    };
  },
  created() {
    this.peoples = peoples[this.$route.params.id];
  },
  components: {
    crumbs
  }
};
</script>

<style lang="scss" scoped>
.people {
  text-align: center;
  .crumbs {
    padding: 15px;
  }
  .title {
    height: 230px;
    background: #461b93;
    color: #fff;
    h2 {
      line-height: 230px;
    }
  }
  .introduce {
    letter-spacing: 1px;
    padding: 15px;
    h3{
      color: #333;
    }
    p {
      text-align: center;
      color: #6a3cbc;
      font-size: 12px;
    }
    .things {
      text-align: left;
      padding-top: 30px;
      h4 {
        font-size: 16px;
        padding: 8px 0;
        color: #333;
      }
      p {
        background: #6a3cbc;
        color: #fff;
        border-radius: 2px;
        padding: 5px;
        text-align: right;
        padding: 10px;
        font-size: 13px;
        letter-spacing: 0.5px;
      }
    }
  }
}
</style>



